:root {
  --font-body: "Open Sans", Arial, sans-serif;
}

:root {
  --clr-letter: hsl(0, 0%, 0%);
  --bg-color: hsl(0, 0%, 100%);
  --clr-correct: hsl(112, 50%, 66%);
  --clr-absent: hsl(197, 10%, 87%);
  --clr-present: hsl(30, 79%, 68%);
  --letter-border: hsl(0, 0%, 77%);
  --border-added: hsl(0, 0%, 55%);
}

:root.darkmode {
  --clr-letter: hsl(0, 0%, 100%);
  --bg-color: hsl(0, 0%, 0%);
  --clr-correct: hsl(112, 37%, 50%);
  --clr-absent: hsl(204, 4%, 77%);
  --clr-present: hsl(30, 54%, 56%);
  --letter-border: hsl(0, 1%, 72%);
  --border-added: hsl(0, 1%, 49%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
input,
p {
  margin: 0;
}

@media (prefers-color-scheme: dark) {
  body.dark-scheme {
    background: #333;
    color: white;
  }

  .night.dark-scheme {
    background: black;
    color: #ddd;
  }
}
body {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  min-height: 100vh;
  padding: 0;
  font-family: var(--font-body);
  font-size: var(--fs-300);
  background-color: var(--bg-color);
  color: var(--clr-letter);
  line-height: 1.5;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.flow > :where(:not(:first-child)) {
  margin-top: var(--flow-space, 1rem);
}

h1 {
  padding-top: 1.125rem;
  font-size: var(--fs-700);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  /* added line */
  border: 0;
}

:root {
  --fs-300: 0.875rem;
  --fs-400: 1rem;
  --fs-500: 1.25rem;
  --fs-600: 1.75rem;
  --fs-700: 1.75rem;
}
@media (min-width: 34.4375em) {
  :root {
    --fs-300: 1.125rem;
    --fs-400: 1.375rem;
    --fs-500: 1.75rem;
    --fs-600: 2.375rem;
    --fs-700: 2.5rem;
  }
}

h1,
h2,
h3 {
  text-align: center;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  position: relative;
  width: Min(20rem, 95%);
  margin-inline: auto;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid {
  display: grid;
}

.flex, .grid {
  gap: var(--gap, 1rem);
}

.pop-in {
  animation: popIn 100ms;
}

@keyframes popIn {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  40% {
    transform: scale(1.1);
    opacity: 1;
  }
}
.flip-in {
  animation: flipIn 250ms forwards;
}

.flip-out {
  animation: flipOut 250ms forwards;
}

@keyframes flipIn {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(-90deg);
  }
}
@keyframes flipOut {
  from {
    transform: rotateX(-90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}
.leap {
  animation: hopping 250ms 1s forwards;
}

@keyframes hopping {
  20% {
    transform: translateY(-12px);
  }
  60% {
    transform: translateY(0);
  }
}
.board {
  grid-template-rows: repeat(6, 1fr);
  gap: 0.5rem;
}
.board__row {
  --gap: 0.5rem;
  grid-template-columns: repeat(5, 1fr);
}
.board__row > .tile-box {
  flex: 1;
  aspect-ratio: 1;
  text-transform: uppercase;
  color: var(--white);
}
.board__row > .tile-box[data-state=empty] {
  border: 2px solid var(--letter-border);
}
.board__row > .tile-box[data-state=added] {
  border: 2px solid var(--border-added);
  color: var(--clr-letter);
}
.board__row > .tile-box[data-state=absent] {
  background: var(--clr-absent);
}
.board__row > .tile-box[data-state=misplaced] {
  background: var(--clr-present);
}
.board__row > .tile-box[data-state=found] {
  background: var(--clr-correct);
}
.board__row > .tile-box span {
  font-size: var(--fs-600);
  font-weight: 700;
}

.mode-toggle {
  width: min(85px, 33%);
  position: absolute;
  right: 0;
  border: 0;
  border-radius: 1.25rem;
  padding: 0;
  background-color: transparent;
}
.mode-toggle .btn-bg {
  position: absolute;
  inset: 0;
  border-radius: 1.25rem;
  background-color: var(--clr-letter);
  z-index: -1;
}
.mode-toggle div {
  --gap: 0.9375rem;
}
.mode-toggle div input {
  opacity: 0;
  flex-basis: 38%;
  height: 2.0625rem;
  cursor: pointer;
}
.mode-toggle .toggle {
  position: absolute;
  top: 5%;
  left: 3%;
  width: 35%;
  height: 94%;
  border-radius: 50%;
  transition: left 250ms ease-in-out;
  background: var(--clr-present);
}
.mode-toggle div > #dark:checked ~ .toggle {
  left: 60%;
}